.cursor {
  position: fixed;
  z-index: 100;
  top: -20px;
  left: -20px;
  // transform: 3d(-50%, -50%, 0);
  width: 40px;
  height: 40px;
  background-color: transparent;
  mix-blend-mode: exclusion;
  pointer-events: none;

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #fff;
  }

  .pulse-ring {
    position: absolute;
    background-color: inherit;
    background-color: #fff;
    height: 100%;
    width: 100%;
    border-radius: 100%;
    opacity: 0.8; 
  }

  @media screen and (pointer:coarse) {
    display: none;
  }
}
